import {deepPurple, green, orange, purple} from "@material-ui/core/colors";
import lightBlue from "@material-ui/core/colors/lightBlue";
import Divider from "@material-ui/core/Divider";
import Grid from "@material-ui/core/Grid";
import React, {useEffect, useState} from "react";
import {useHistory} from "react-router";
import Store from "../../../../redux/store";
import {TextField, Button, Typography, Box} from "@material-ui/core";
import translate from "../../../../translate";
import axios from "axios";
import {connect} from "react-redux";
import {Search} from "@material-ui/icons";
import WaveDivider from "../side-components/wave-divider";

const mapStateToProps = (state) => {
   return {
      baseUrl: state.baseUrl,
      blocks: state.homeBlocks,
      slides: state.appSlides,
      filterItems: state.filterItems,
   };
};

function Home(props) {
   const {push} = useHistory();
   const [slides, setSlides] = useState(props.slide || [])
   const servicesSections = [
      {
         name: 'producers',
         title: '',
         excerpt: '                                 ',
         description: '                                                .                             ...               .',
         direction: 'rtl',
         icon: props.baseUrl + '/icons/others/industry.png',
         backgroundColor: lightBlue.A100,
         buttons: [
            {
               label: '  ',
               onClick: () => {
                  changeFilterItems([
                     {
                        name:'class',
                        value:'idp',
                     },
                  ])
                  push('/explore')
               },
            },
            {
               label: '     ',
               onClick: () => {
                  changeFilterItems([
                     {
                        name:'class',
                        value:'prd',
                     },
                     {
                        name:'insideOfIdp',
                        value:true,
                     }
                  ])
                  push('/explore')
               },
            }
         ],
      },
      {
         name: 'workshops',
         title: ' ',
         excerpt: '                   ',
         description: '                      .        .                   (   )        .                    .',
         direction: 'ltr',
         icon: props.baseUrl + '/icons/others/workshop2.png',
         backgroundColor: green.A100,
         buttons: [
            {
               label: '     ',
               onClick: () => {
                  push({
                     pathname: '/explore?class=prd&type=factory&isInIdp=true',
                     state: {
                        class: 'prd',
                        type: 'factory',
                        isInIdp: true,
                     }
                  })
               },
            },
            {
               label: '     ',
               onClick: () => {
                  push({
                     pathname: '/explore?class=prd&type=factory&isInIdp=false',
                     state: {
                        class: 'prd',
                        type: 'factory',
                        isInIdp: false,
                     }
                  })
               },
            }
         ],
      },
      {
         name: 'exhibitions',
         title: ' ',
         excerpt: '                              ',
         description: '           .                       .       :                                            ...        .',
         direction: 'rtl',
         icon: props.baseUrl + '/icons/others/exhibition.png',
         backgroundColor: orange.A100,
         buttons: [],
      },
   ];

   useEffect(() => {
      Store.dispatch({
         type: "navTabValue",
         payload: 0,
      });
      slides.length === 0 && getAppSlides();
   }, [])

   function getAppSlides() {
      const url = `${Store.getState().baseUrl}/api/appSlides`;
      axios.post(url).then((e) => {
         setSlides(e.data);
         Store.dispatch({
            type: "appSlides",
            payload: e.data,
         });
      }).catch((e) => {
         console.log(e);
         console.log(e.response);
      });
   }

   function changeFilterItems(newData) {
      let filterItems = props.filterItems;
      newData.map(data => {
         let item = filterItems.find(x => x.name === data.name);
         let itemIndex = filterItems.findIndex(x => x.name === data.name);
         if (item) {
            item.value = data.value;
            if (data.label) {
               item.label = data.label;
            }
            filterItems[itemIndex] = item;
         }
      })
      Store.dispatch({
         type:'filterItems',
         payload:filterItems,
      })
      Store.dispatch({
         type: "getBlocks",
         payload: true,
      });
   }

   return (
      <div
         style={{
            height: "calc(100vh - 60px)",
            marginTop: 60,
            backgroundColor: "#f0f8ff",
            overflowY: 'auto'
         }}
      >
         <div style={{
            width: '100%',
            height: 'calc(100vh - 60px)',
            backgroundImage: `url("${props.baseUrl}/images/backgrounds/factory.jpg")`,
            backgroundSize: 'cover',
            backgroundPosition: 'center',
            display: 'flex',
            flexFlow: 'column',
            alignItems: 'center',
            position: 'relative',
         }}>
            <div style={{
               backgroundColor: "rgba(255,255,255,0.9)",
               padding: 60,
               borderRadius: 10,
               display: 'flex',
               flexFlow: 'column',
               justifyContent: 'center',
               alignItems: 'center',
               width: '60%',
               marginTop: 30,
            }}>
               <img
                  src={props.baseUrl + '/icons/pwa/logo.png'}
                  alt='samtbook'
                  style={{
                     width: 150,
                  }}
               />
               <div style={{height: 50}}/>
               <Typography variant='h4'>
                  {translate('  -    ')}
               </Typography>
               <div style={{height: 50}}/>
               <div style={{
                  display: 'flex',
                  width: '100%',
               }}>
                  <Button
                     variant='contained'
                     color='primary'
                     style={{
                        marginLeft: 10,
                        width: 100
                     }}>
                     <Search/>
                  </Button>
                  <TextField
                     size='medium'
                     variant='outlined'
                     label={translate('    ')}
                     fullWidth
                  />
               </div>
            </div>
            <div
               style={{
                  width: '100%',
                  position: 'absolute',
                  bottom: -5,
               }}
            >
               {/*<WaveDivider color={lightBlue.A100}/>*/}
            </div>
         </div>
         <div>
            {servicesSections.map((section, key) => {
               return (
                  <Box
                     key={key}
                     style={{
                        minHeight: 300,
                        padding: 100,
                        backgroundColor: section.backgroundColor,
                     }}
                  >
                     <Grid container spacing={4}>
                        {section.direction === 'ltr' && (
                           <Grid item xs={6}>
                              <Box style={{
                                 display: 'flex',
                                 flexFlow: 'column',
                                 justifyContent: 'center',
                                 alignItems: 'center',
                              }}>
                                 <img
                                    src={section.icon}
                                    alt={section.title}
                                    style={{
                                       width: '100%',
                                    }}
                                 />
                              </Box>
                           </Grid>
                        )}
                        <Grid item xs={6}>
                           <Box style={{
                              display: 'flex',
                              flexFlow: 'column',
                              justifyContent: 'center',
                              alignItems: 'center',
                              height: '100%',
                           }}>
                              <Typography variant='h4'>{translate(section.title)}</Typography>
                              <div style={{height: 20}}/>
                              <Typography variant='h6'
                                          style={{textAlign: "center"}}>{translate(section.excerpt)}</Typography>
                              <Divider style={{
                                 width: '90%',
                                 margin: '15px 0',
                                 borderBottom: '1px solid grey'
                              }}/>
                              <Typography variant='body1'
                                          style={{textAlign: "center"}}>{translate(section.description)}</Typography>
                              <div style={{height: 20}}/>
                              {section.buttons.map((button, key) => {
                                 return (
                                    <Button
                                       variant='contained'
                                       color='primary'
                                       onClick={button.onClick}
                                       style={{
                                          marginBottom: 20,
                                       }}
                                       fullWidth
                                    >
                                       {button.label}
                                    </Button>
                                 )
                              })}
                           </Box>
                        </Grid>
                        {section.direction === 'rtl' && (
                           <Grid item xs={6}>
                              <Box style={{
                                 display: 'flex',
                                 flexFlow: 'column',
                                 justifyContent: 'center',
                                 alignItems: 'center',
                              }}>
                                 <img
                                    src={section.icon}
                                    alt={section.title}
                                    style={{
                                       width: '100%',
                                    }}
                                 />
                              </Box>
                           </Grid>
                        )}
                     </Grid>
                  </Box>
               )
            })}
         </div>
      </div>
   );
}

export default connect(mapStateToProps)(Home);
